@media only screen and (min-width: 600px) {
  .griditem:nth-child(4) .info {
    bottom: 7%;
  }
  .griditem:nth-child(4) .info h4 {
    margin: auto auto;
    padding: 4px 16px;
  }
}
@media only screen and (max-width: 600px) {
  .griditem .info {
    bottom: 7%;
  }
  .griditem .info h4 {
    margin: auto auto;
    padding: 4px 16px;
  }
}
.griditem .info {
  position: absolute;
  z-index: 1;
  margin: 0 8%;
  width: 84%;
  height: auto;
  bottom: 7%;
}
.griditem .info a {
  padding: 1px 0px 0px;
  top: 2px;
  position: relative;
  left: -2px;
  display: table-cell;
}
.griditem .info .wrap {
  background: white;
  text-align: justify;
  padding: 4px 8px;
  border-top-right-radius: 4px;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}
.griditem .info .wrap .link {
  padding: 3px 5px;
  overflow: auto;
  display: table-cell;
}
.griditem .info .wrap .link a {
  float: right;
}
.griditem .info h4 {
  width: fit-content;
  background: white;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  padding: 4px 8px;
  color: #243449;
  -webkit-transition: color 0.2s;
  -moz-transition: color 0.2s;
  -ms-transition: color 0.2s;
  -o-transition: color 0.2s;
  transition: color 0.2s;
}
.griditem .info h4:hover, .griditem .info h4:focus {
  color: #1d79fc;
}