.griditem form.ui.pag {
  display: table;
  left: 50%;
  right: 50%;
  margin-right: -50%;
  position: absolute;
  bottom: 0px;
  transform: translate(-50%, -50%);
}

.articleInfo {
  margin: 10px 3% 17px;
  border-bottom: #e4e4e4 solid 1px;
  padding: 0 0 15px;
  position: relative;
}
.articleInfo:last-of-type {
  border-bottom: none;
  margin-bottom: 8px;
  padding-bottom: 0px;
}
.articleInfo .thumb, .articleInfo .heading {
  float: left;
  vertical-align: top;
}
.articleInfo .wrapper {
  width: 100%;
}
.articleInfo .thumb {
  width: 30%;
  margin-right: 9px;
  padding-bottom: 30%;
  position: relative;
}
.articleInfo .thumb a {
  padding: 0px;
  position: absolute;
  overflow: hidden;
}
.articleInfo .thumb a img {
  object-fit: cover;
  width: 100%;
  min-height: 100%;
  transform: translate(-50%, -50%) scale(1.05);
  -webkit-transition: transform 0.2s ease-in-out;
  -moz-transition: transform 0.2s ease-in-out;
  -ms-transition: transform 0.2s ease-in-out;
  -o-transition: transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out;
}
.articleInfo .thumb a:hover > img {
  -webkit-transform: translate(-50%, -50%) scale(1.15);
  -moz-transform: translate(-50%, -50%) scale(1.15);
  -ms-transform: translate(-50%, -50%) scale(1.15);
  -o-transform: translate(-50%, -50%) scale(1.15);
  transform: translate(-50%, -50%) scale(1.15);
}
.articleInfo .heading {
  top: 3px;
  width: calc(70% - 9px);
}
.articleInfo .heading .tags {
  display: inline-block;
  max-width: 100%;
  text-transform: uppercase;
}
.articleInfo .heading .tags a {
  background-color: #f1f6f9;
  margin-right: 6px;
  margin-bottom: 6px;
  width: auto;
}
.articleInfo .heading .tags a:hover, .articleInfo .heading .tags a:focus {
  background-color: #f1f1f1;
}
.articleInfo .heading .title {
  font-family: "Roboto Slab";
  font-size: 20px;
  font-weight: 600;
  text-align: justify;
  display: block;
  color: #4f5e72;
  padding: 3px;
}
.articleInfo .heading .title:hover, .articleInfo .heading .title:focus {
  color: #1d79fc;
}
.articleInfo .desc {
  padding: 3px;
}
.articleInfo .more {
  margin-top: 2px;
  float: right;
  position: relative;
  width: initial;
}
.articleInfo::after {
  content: "";
  clear: both;
  display: table;
}

@media only screen and (max-width: 1007px) {
  .articleInfo .thumb {
    width: 200px;
    padding-bottom: 200px;
  }
  .articleInfo .heading {
    width: calc(100% - 200px - 9px);
  }
}
@media only screen and (max-width: 700px) {
  .articleInfo .thumb {
    width: 150px;
    padding-bottom: 150px;
  }
  .articleInfo .heading {
    width: calc(100% - 150px - 9px);
  }
}
@media only screen and (max-width: 400px) {
  .articleInfo .thumb {
    width: 100px;
    padding-bottom: 100px;
  }
  .articleInfo .heading {
    width: calc(100% - 100px - 9px);
  }

  .griditem form.ui.pag {
    bottom: -10px;
  }
}