footer {
  background-color: #3e4a55;
}
footer .upper {
  padding: 0 16px;
  display: table;
  width: 100%;
}
footer .upper ul {
  margin-top: 10px;
}
footer .upper h5 {
  color: #7690b6;
  padding-left: 6px;
}
footer .upper a {
  font-weight: normal;
  color: #dee1ef;
}
footer .upper li a {
  line-height: 25px;
}
footer .upper li a:hover {
  color: #7690b6;
}
footer .upper .wrapper {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  justify-content: space-between;
  margin: 40px 0;
}
footer .upper .wrapper #logo {
  display: table;
}
footer .upper .wrapper nav {
  flex-shrink: 0;
  top: 0;
  text-align: left;
  line-height: 20px;
}
footer .lower {
  background: #28292f;
  display: table;
  width: 100%;
}
footer .lower .wrapper {
  padding: 16px;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
}
footer .lower .wrapper span {
  margin-right: auto;
}
footer .lower .wrapper a {
  color: #606889;
  margin-left: 15px;
}
footer .lower .wrapper a:hover {
  color: #c8d0dc;
}

@media only screen and (max-width: 800px) {
  footer {
    justify-content: initial;
  }
  footer #logo {
    margin: 0 auto;
  }
  footer nav {
    width: 100%;
    text-align: center !important;
    margin: 10px 0;
  }
  footer nav li {
    margin-top: 5px;
  }
  footer .lower .wrapper span {
    order: 4;
  }
  footer .lower .wrapper .box, footer .lower .wrapper span {
    width: 100%;
    text-align: center;
    line-height: 25px;
    margin-top: 5px;
  }
  footer .lower .wrapper .box a, footer .lower .wrapper span a {
    margin: 0;
  }
}