.menu {
  font-family: Roboto;
  font-size: 17px;
  font-weight: 500;
  text-transform: uppercase;
}
.menu li a {
  color: #243449;
  font-weight: 500;
}
.menu li a:focus {
  color: #1d79fc;
}
.menu li a:hover {
  color: #1d79fc;
}

@media only screen and (max-width: 992px) {
  .menu {
    background: white;
    position: fixed;
    height: 100%;
    padding-top: 15px;
    top: 0;
    z-index: -2;
    right: -150%;
    width: 200px;
    -webkit-transition: 0.2s;
    -moz-transition: 0.2s;
    -ms-transition: 0.2s;
    -o-transition: 0.2s;
    transition: 0.2s;
    -webkit-box-shadow: 0px 5px 14px 0px rgba(0, 0, 0, 0.15);
    -moz-box-shadow: 0px 5px 14px 0px rgba(0, 0, 0, 0.15);
    box-shadow: 0px 5px 14px 0px rgba(0, 0, 0, 0.15);
  }
  .menu li a {
    display: block;
    padding: 10px 15px;
  }
  .menu .submenu {
    padding-left: 10px;
  }
  .menu .submenu li a {
    font-weight: 400;
  }
}
@media only screen and (min-width: 992px) {
  nav {
    position: relative;
    top: 5px;
  }
  nav .menu {
    list-style: none;
    float: right;
  }
  nav .menu .menuitem:hover:after {
    content: "";
    display: block;
    height: 17px;
  }
  nav .menu li {
    float: left;
    padding: 5px 0px 5px 0px;
    position: relative;
  }
  nav .menu li a {
    padding: 3px 10px 3px 10px;
  }
  nav .menu li a:focus + .submenu {
    left: 0px;
    opacity: 1;
    margin-top: 23px;
    visibility: visible;
  }
  nav .menu li:hover > .submenu {
    opacity: 1;
    margin-top: 23px;
    visibility: visible;
  }
  nav .menu .submenu {
    position: absolute;
    opacity: 0;
    visibility: hidden;
    margin-top: 10px;
    background-color: white;
    border-radius: 6px;
    color: #243449;
    -webkit-box-shadow: 0px 5px 14px 0px rgba(0, 0, 0, 0.15);
    -moz-box-shadow: 0px 5px 14px 0px rgba(0, 0, 0, 0.15);
    box-shadow: 0px 5px 14px 0px rgba(0, 0, 0, 0.15);
    -webkit-transition: opacity 0.25s, margin-top 0.25s, visibility 0.25s;
    -moz-transition: opacity 0.25s, margin-top 0.25s, visibility 0.25s;
    -ms-transition: opacity 0.25s, margin-top 0.25s, visibility 0.25s;
    -o-transition: opacity 0.25s, margin-top 0.25s, visibility 0.25s;
    transition: opacity 0.25s, margin-top 0.25s, visibility 0.25s;
  }
  nav .menu .submenu:before {
    content: "";
    position: absolute;
    z-index: -1;
    left: 23px;
    width: 0;
    height: 0;
    border: 5px solid;
    border-color: transparent transparent white white;
    transform-origin: 0 0;
    transform: rotate(135deg);
    -webkit-box-shadow: -1px 1px 7px -1px rgba(0, 0, 0, 0.15);
    -moz-box-shadow: -1px 1px 7px -1px rgba(0, 0, 0, 0.15);
    box-shadow: -1px 1px 7px -1px rgba(0, 0, 0, 0.15);
  }
  nav .menu .submenu:focus-within {
    left: 0px;
    opacity: 1;
    margin-top: 23px;
    visibility: visible;
  }
  nav .menu .submenu li {
    float: none;
    margin-top: -11px;
    padding-bottom: 16px;
  }
  nav .menu .submenu li:first-child {
    margin-top: 0px;
    padding-top: 15px;
  }
  nav .menu .submenu li a {
    display: block;
    border-radius: 3px;
  }
  nav .bg {
    width: 72px;
    height: 27px;
    background-color: white;
    position: absolute;
    z-index: -1;
    border-radius: 6px;
    -webkit-box-shadow: 0px 3px 7px 0px rgba(0, 0, 0, 0.15);
    -moz-box-shadow: 0px 3px 7px 0px rgba(0, 0, 0, 0.15);
    box-shadow: 0px 3px 7px 0px rgba(0, 0, 0, 0.15);
    -webkit-transition: width 0.3s, left 0.3s;
    -moz-transition: width 0.3s, left 0.3s;
    -ms-transition: width 0.3s, left 0.3s;
    -o-transition: width 0.3s, left 0.3s;
    transition: width 0.3s, left 0.3s;
  }
}