.slider {
  overflow: hidden;
}
.slider img {
  position: relative;
  left: 5%;
  pointer-events: none;
}
.slider .wrapper {
  position: absolute;
  top: 10%;
  right: 3%;
  background: white;
  padding: 7px;
  border-radius: 4px;
  width: 52%;
}
.slider .wrapper p {
  margin-top: 5%;
}
.slider .wrapper .container {
  margin-top: 5%;
  right: 0%;
  position: absolute;
  width: 250px;
}
.slider .wrapper .container button {
  margin-right: 5%;
  display: inline;
}
.slider .wrapper .container button:last-of-type {
  margin-right: 0;
}

@media only screen and (max-width: 600px) {
  .slider img {
    left: 2%;
  }
  .slider .wrapper {
    top: 4%;
  }
}
@media only screen and (max-width: 450px) {
  .slider img {
    left: 2%;
  }
  .slider .wrapper {
    top: 4%;
    width: 70%;
  }
  .slider .wrapper .container button {
    margin-right: 3%;
  }
}