.griditem .head.wrapper {
  position: absolute;
  text-align: center;
  top: 65%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, 65%);
}
.griditem .head.wrapper h2 {
  padding: 7px 20px;
  border-radius: 4px;
  text-transform: uppercase;
}
.griditem .head.wrapper span {
  padding: 7px;
  margin-top: -2px;
  display: inline-block;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}
.griditem .head.wrapper h2, .griditem .head.wrapper span {
  background-color: #fff;
}