.contact {
  padding: 16px;
}
.contact .wrapper, .contact .flex {
  padding: 2% 3% 2% 3%;
}
.contact .wrapper {
  display: table;
  margin: auto auto;
  padding: 2% 3% 0% 3%;
}
.contact .wrapper:last-of-type {
  padding-bottom: 2%;
}
.contact .form {
  width: 100%;
  line-height: 25px;
}
.contact .form input, .contact .form select {
  margin: 13px 0;
  width: 100%;
}
.contact .form textarea {
  width: 100%;
  margin: 13px 0 0 0;
  resize: vertical;
  min-height: 200px;
}
.contact .form .dropzone {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  margin-top: 2px;
  margin-bottom: 10px;
  border-width: 2px;
  border-radius: 4px;
  border-color: #8a8ea0;
  border-style: dashed;
  background-color: #fafafa;
  color: #bdbdbd;
  cursor: pointer;
  outline: none;
  -webkit-transition: all 0.15s ease-in-out;
  transition: all 0.15s ease-in-out;
  -webkit-transition-property: border, background-color;
  transition-property: border, background-color;
}
.contact .form .dropzone p {
  text-align: center;
  text-indent: inherit;
}
.contact .form .dropzone:hover {
  background-color: #ececec;
}
.contact .flex {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.contact .flex .box {
  line-height: 20px;
}

@media only screen and (max-width: 800px) {
  .contact .flex {
    justify-content: initial;
  }
  .contact .flex .box {
    width: 100%;
    text-align: center;
    margin: 10px 0;
  }
  .contact .flex .box table {
    margin: auto;
  }
}
