@media only screen and (max-width: 992px) {
  .menu {
    background: white;
    position: fixed;
    height: 100%;
    padding-top: 15px;
    top: 0;
    z-index: -2;
    right: -150%;
    width: 200px;
    -webkit-transition: 0.2s;
    transition: 0.2s;
    box-shadow: 0px 5px 14px 0px rgba(0, 0, 0, 0.15);
  }
  .menu li a {
    display: block;
    padding: 10px 15px;
  }
  .menu .submenu {
    padding-left: 10px;
  }
  .menu .submenu li a {
    font-weight: 400;
  }
}
@media only screen and (min-width: 992px) {
  nav {
    position: relative;
    top: 5px;
  }
  nav .menu {
    list-style: none;
    float: right;
  }
  nav .menu .menuitem:hover:after {
    content: "";
    display: block;
    height: 17px;
  }
  nav .menu li {
    float: left;
    padding: 5px 0px 5px 0px;
    position: relative;
  }
  nav .menu li a {
    padding: 3px 10px 3px 10px;
  }
  nav .menu li a:focus + .submenu {
    left: 0px;
    opacity: 1;
    margin-top: 23px;
    visibility: visible;
  }
  nav .menu li:hover > .submenu {
    opacity: 1;
    margin-top: 23px;
    visibility: visible;
  }
  nav .menu .submenu {
    position: absolute;
    opacity: 0;
    visibility: hidden;
    margin-top: 10px;
    background-color: white;
    border-radius: 6px;
    color: #243449;
    box-shadow: 0px 5px 14px 0px rgba(0, 0, 0, 0.15);
    -webkit-transition: opacity 0.25s, margin-top 0.25s, visibility 0.25s;
    transition: opacity 0.25s, margin-top 0.25s, visibility 0.25s;
  }
  nav .menu .submenu:before {
    content: "";
    position: absolute;
    z-index: -1;
    left: 23px;
    width: 0;
    height: 0;
    border: 5px solid;
    border-color: transparent transparent white white;
    -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
    -webkit-transform: rotate(135deg);
            transform: rotate(135deg);
    box-shadow: -1px 1px 7px -1px rgba(0, 0, 0, 0.15);
  }
  nav .menu .submenu:focus-within {
    left: 0px;
    opacity: 1;
    margin-top: 23px;
    visibility: visible;
  }
  nav .menu .submenu li {
    float: none;
    margin-top: -11px;
    padding-bottom: 16px;
  }
  nav .menu .submenu li:first-child {
    margin-top: 0px;
    padding-top: 15px;
  }
  nav .menu .submenu li a {
    display: block;
    border-radius: 3px;
  }
  nav .bg {
    width: 72px;
    height: 27px;
    background-color: white;
    position: absolute;
    z-index: -1;
    border-radius: 6px;
    box-shadow: 0px 3px 7px 0px rgba(0, 0, 0, 0.15);
    -webkit-transition: width 0.3s, left 0.3s;
    transition: width 0.3s, left 0.3s;
  }
}
@media only screen and (max-width: 992px) {
  .hamburger {
    width: 30px;
    height: 30px;
    padding: 3px;
    border-radius: 4px;
    position: relative;
    border: 2px solid transparent;
    cursor: pointer;
    float: right;
    margin: 7px 3px 7px 0px;
    outline: none;
  }
  .hamburger:focus {
    color: #243449;
    border: 2px solid #0066ff;
    box-shadow: 0 0 0 2px rgba(165, 201, 227, 0.8);
    -webkit-transition: color 0.2s;
    transition: color 0.2s;
  }
  .hamburger:hover span {
    background: #2e84ff;
  }
  .hamburger span {
    display: block;
    z-index: -1;
    position: relative;
    height: 22%;
    width: 100%;
    border-radius: 2px;
    opacity: 1;
    left: 0;
    background: #243449;
    -webkit-transition: 0.2s;
    transition: 0.2s;
    -webkit-transform-origin: left center;
    transform-origin: left center;
  }
  .hamburger span:nth-child(1) {
    top: 0%;
  }
  .hamburger span:nth-child(2) {
    top: 19%;
  }
  .hamburger span:nth-child(3) {
    top: 40%;
  }

  .open span {
    left: 15%;
  }
  .open span:nth-child(1) {
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    top: 5%;
  }
  .open span:nth-child(2) {
    width: 0%;
    opacity: 0;
  }
  .open span:nth-child(3) {
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
    top: 35%;
  }
  .open + .menu {
    right: 0;
  }
}
