.scrollbar {
  height: 80% !important;
}

.gallery button {
  visibility: hidden;
}
.gallery .all {
  top: 0.8rem;
  right: 10px;
  position: relative;
  text-align: right;
}
.gallery .all a {
  display: initial;
}
.gallery .wrapper {
  display: grid;
  padding: 2px 10px;
  grid-template-columns: repeat(auto-fill, minmax(30%, 1fr));
  grid-auto-rows: 1fr;
  grid-gap: 10px;
}
.gallery .wrapper:before {
  content: "";
  width: 0;
  padding-bottom: 100%;
  grid-row: 1/1;
  grid-column: 1/1;
}
.gallery .wrapper > *:first-child {
  grid-row: 1/1;
  grid-column: 1/1;
}
.gallery .wrapper .galleryItem a {
  overflow: hidden;
  padding: 0;
}
.gallery .wrapper .galleryItem a img {
  border-radius: 5px;
  object-fit: cover;
  width: 100%;
  min-height: 100%;
  transform: translate(-50%, -50%) scale3d(1, 1, 1);
  -webkit-transition: transform 0.2s ease-in-out;
  -moz-transition: transform 0.2s ease-in-out;
  -ms-transition: transform 0.2s ease-in-out;
  -o-transition: transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out;
}
.gallery .wrapper .galleryItem:hover > a > img {
  -webkit-transform: translate(-50%, -50%) scale3d(1.1, 1.1, 1.1);
  -moz-transform: translate(-50%, -50%) scale3d(1.1, 1.1, 1.1);
  -ms-transform: translate(-50%, -50%) scale3d(1.1, 1.1, 1.1);
  -o-transform: translate(-50%, -50%) scale3d(1.1, 1.1, 1.1);
  transform: translate(-50%, -50%) scale3d(1.1, 1.1, 1.1);
}

@media only screen and (max-width: 1009px) {
  .scrollbar {
    height: 73% !important;
  }
}
@media only screen and (max-width: 600px) {
  .gallery .all {
    visibility: hidden;
  }
  .gallery button {
    visibility: visible;
    position: absolute;
    top: 87%;
    left: 50%;
    transform: translate(-50%, 0);
  }
}